import React, { useState } from 'react';
import style from './ProfileInfo.module.css';
import Preloader from '../../common/preloader/preloader';
import ProfileStatus from './ProfileStatus';
import ProfileStatusWithHooks from './ProfileStatusWithHooks';
import userPhoto from '../../../assets/images/user2.jpg'
import { ProfileDataFormRedux } from './ProfileDataForm';

const ProfileInfo = ({profile,status,updateStatus,isOwner,saveProfile, ...props}) => {

    let [editMode, setEditMode] = useState(false)

    if(!profile){
        return <Preloader />
    }
    const onMainPhotoSelected = (e) => {
        if(e.target.files.length) {
            props.savePhoto(e.target.files[0])
        }
    }
    const onSubmit = (formData) =>{
        saveProfile(formData).then(()=>{
            setEditMode(false)
        })
     }

    return (
        <div >
            <div className={style.discriptionBlock}>
                <img src={profile.photos.large || userPhoto} className={style.mainPhoto} />
                {isOwner && <input type={'file'} onChange={onMainPhotoSelected} />}

                {editMode
                    ? <ProfileDataFormRedux initialValues={profile} profile={profile} onSubmit={onSubmit}/>
                    : <ProfileData goToEditMode={()=> {setEditMode(true)}} 
                                   profile={profile} 
                                   isOwner={isOwner}/>}

                <ProfileStatusWithHooks status={status}
                    updateStatus={updateStatus} />
            </div>
        </div>
    )
}   

const ProfileData = ({ profile, isOwner, goToEditMode }) => {
    return (
        <div>
            {isOwner && <div><button onClick={goToEditMode}>edit</button></div>}
            <div>
                <b>Full name:</b> {profile.fullName}
            </div>
            <div>
                <b>Looking for a JOB:</b> {profile.lookingForAJob ? 'YES' : 'NO'}
            </div>
            {profile.lookingForAJob &&
                <div>
                    <b>My proffesional skills:</b> {profile.lookingForAJobDescription}
                </div>
            }
            <div>
                <b>About me:</b> {profile.aboutMe}
            </div>
            <div>
                <b>Contacts:</b> {Object.keys(profile.contacts).map(key => {
                    return <Contact key={key} contactTitle={key} contactValue={profile.contacts[key]} />
                })}
            </div>
        </div>
    )
}


const Contact = ({ contactTitle, contactValue }) => {
    return <div className={style.contact}> <b>{contactTitle}:</b> {contactValue}</div>
}
export default ProfileInfo;